<template>
  <div class="bookstore-detail">
    <div class="book-introduction">
      <div class="left-demo-card">
        <div class="left-demo-body">
          <div class="left-img-box flex-center">
            <img
              :src="coverFiles.length > 0 ? coverFiles[currImg].fullUrl : ''"
            />
          </div>
          <div class="left-small-img">
            <img
              v-for="(item, index) in coverFiles"
              :key="index + Math.random()"
              :src="item.fullUrl"
              :class="index == currImg ? 'demo-card-box-img-checked' : ''"
              @click="changeSrc(index)"
            />
          </div>
        </div>
        <div class="left-demo-bottom">
          <div @click="addBookshelf()">
            <img
              :src="
                require('@/assets/icon/book1' +
                  (isBookshelf ? 'ed.png' : '.png'))
              "
              alt=""
            />
            {{ isBookshelf ? "已加入书架" : "加入书架" }}
          </div>
        </div>
      </div>
      <div class="right-info">
        <div class="title">
          <div class="book-label">课程</div>
          <span>{{ detail.resourceName }}</span>
        </div>
        <div class="book-intro" v-if="detail.authorRecommendation">
          <div
            class="defaultIntro"
            @mouseenter="enterShow"
            @mouseleave="leaveShow"
          >
            <span class="twointro"
              >{{ detail.authorRecommendation | ellipsis(120) }}
              <span class="font" v-if="detail.authorRecommendation.length > 120"
                >更多</span
              ></span
            >
            <div v-if="showMore" class="allIntro">
              <span>{{ detail.authorRecommendation }}</span>
            </div>
          </div>
        </div>
        <div class="label">
          <span v-for="(item, index) in keyword" :key="index">{{ item }}</span>
        </div>
        <div class="book-intro-info">
          <div>
            <span>讲师：</span>
            <el-tooltip
              :content="detail.lecturer"
              placement="top"
              v-ak-tooltip-auto-show
            >
              <span class="ellipsis-aRow">{{
                detail.lecturer == null || detail.lecturer == ""
                  ? "-"
                  : detail.lecturer
              }}</span>
            </el-tooltip>
          </div>
          <!-- <div>
            <span>特殊标记：</span>
            <span class="ellipsis-aRow">{{
              detail.specialMark == "dollors" ? "四套卷" : "-"
            }}</span>
          </div> -->
          <div>
            <span>教学属性：</span>
            <span class="ellipsis-aRow" v-show="teachClass.label">{{
              teachClass.label || "-"
            }}</span>
          </div>
          <div>
            <span>有效期：</span>
            <span class="ellipsis-aRow" v-if="detail.isValidForever != 0"
              >截止到{{ formatDate(detail.validEndDate) }}</span
            >
            <span style="margin-left: 10rpx" v-if="detail.isValidForever == 0"
              >永久</span
            >
          </div>
        </div>
        <div class="book-intro-info">
          <div>
            <span> 课程年份：</span>
            <span class="ellipsis-aRow">{{ detail.particular || "-" }}</span>
          </div>
          <div>
            <span>教学机构：</span>
            <span class="ellipsis-aRow">{{
              detail.instName == null || detail.instName == ""
                ? "-"
                : detail.instName
            }}</span>
          </div>
          <div></div>
        </div>
        <div class="price">
          <span v-if="!isShop">{{ detail.realPrice | tofixed2("￥") }}</span>
          <span v-if="isShop" class="havebuy">已购买</span>
          <span
            v-if="detail.prices > 0"
            :class="detail.prices == 0 ? 'yprice2' : 'yprice'"
            >{{ detail.prices | tofixed2("￥") }}</span
          >
        </div>
        <div class="sales">
          <div class="sales-volume">
            <!-- <img src="@/assets/icon/quan.png" alt="" />
            优惠：
            <div class="quan1">满50-5</div>
            <div class="quan2">满59打9.5折</div>
            <span @click="getMore">领取更多&gt;</span> -->
          </div>
          <div class="goods-score">
            商品评分：
            <el-rate
              v-model="averageScore"
              disabled
              show-score
              text-color="#D81E06"
              :colors="colors"
              score-template="{value}分"
            >
            </el-rate>
          </div>
        </div>
        <div class="integral-share">
          <div class="double-sales">
            <div class="sales-volume">
              <img src="@/assets/icon/销量预测.png" alt="" />
              销量：{{
                detail.salesVolume == null || detail.salesVolume == 0
                  ? 0
                  : detail.salesVolume
              }}
            </div>
            <div class="integral">
              <!-- <img src="@/assets/icon/积分.png" alt="" />
              赠送积分：15 -->
            </div>
          </div>
          <div class="share-img">
            分享到
            <share :config="$store.state.read.config" />
          </div>
        </div>
        <el-divider></el-divider>
        <!-- 书城能购买 -->
        <div class="buttons" v-if="!isShop && detail.status == 2">
          <el-button @click="openBoundShow">输入授权码</el-button>
          <el-button style="color: #fff" @click="toPay">立即购买</el-button>
          <button class="sucessbtn" type="success" @click="addCart()">
            <i class="el-icon-shopping-cart-2"></i>加入购物车
          </button>
        </div>
        <!-- 书城不能购买 -->
        <div class="buttons" v-if="!isShop && detail.status != 2">
          <button class="buttonNot" @click="openBoundShow">输入授权码</button>
        </div>
        <!-- 已经购买完 -->
        <div class="buttons" v-if="isShop">
          <button class="buttonNot" @click="goLearn()">去学习</button>
        </div>
        <p>温馨提示：此类商品不支持退换货</p>
      </div>
    </div>
    <div class="book-infos">
      <div class="left-book-detail">
        <el-tabs v-model="activeName">
          <!-- 商品详情 乐学 -->
          <el-tab-pane name="five">
            <span slot="label">商品详情</span>
            <courseDetails
              :course-list="courseList"
              :testQuestions="paperList"
              :introduction="detail.introduction"
            ></courseDetails>
          </el-tab-pane>
          <!-- 评价 -->
          <el-tab-pane name="fourth">
            <span slot="label">评价 ( {{ total }} )</span>
            <evaluate-content
              :data-list="evaluateList"
              @refreshList2="refreshList2"
            ></evaluate-content>
            <noData v-if="total == 0"></noData>
            <div class="pagination-box" v-show="total > 0">
              <el-pagination
                class="ou-pagination"
                background
                hide-on-single-page
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryParams.pageNum"
                :page-sizes="[10, 20, 30, 40, 50]"
                :page-size="queryParams.pageSize"
                layout="total, prev, pager, next, sizes, jumper, slot"
                :total="total"
              >
                <el-button @click="evaluation()" class="ou-page-go"
                  >GO</el-button
                >
              </el-pagination>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
      <div class="right-recommend">
        <div class="recommend-special" v-if="recommendSubject.length > 0">
          <div class="title theme-title">推荐专题</div>
          <div
            class="recommend-book"
            v-for="(item, index) in recommendSubject"
            :key="index"
          >
            <div class="img flex-center" @click="Special(item.subjectId)">
              <img :src="item.cover" alt="" />
            </div>
            <el-tooltip
              :content="item.subjectName"
              placement="top"
              v-ak-tooltip-auto-show
            >
              <div class="book-title" @click="Special(item.subjectId)">
                {{ item.subjectName }}
              </div>
            </el-tooltip>
            <div class="book-info">
              <div class="left-label">共{{ item.resourcesNun }}商品</div>
              <div class="price">
                <span class="sellprice ellipsis-aRow">{{
                  item.sellingPrice | tofixed2("￥")
                }}</span>
                <!-- <span
                  class="ellipsis-aRow"
                  :class="item.price == 0 ? 'yprice2' : 'yprice'"
                  >{{ item.price | tofixed2("￥") }}</span
                > -->
              </div>
            </div>
          </div>
        </div>
        <div class="recommend-resource" v-if="recommendGoodsOne.length > 0">
          <div class="title theme-title">推荐商品</div>
          <div
            class="recommend-book"
            v-for="(item, index) in recommendGoodsOne"
            :key="index"
          >
            <div
              class="img flex-center"
              @click="recommended(item.goodsId, item.goodsType)"
            >
              <img :src="item.cover" alt="" />
            </div>
            <div
              class="book-title"
              @click="recommended(item.goodsId, item.goodsType)"
            >
              <img src="@/assets/icon/title.png" alt="" />
              <el-tooltip
                :content="item.goodsTitle"
                placement="top"
                v-ak-tooltip-auto-show
              >
                <p>{{ item.goodsTitle }}</p>
              </el-tooltip>
            </div>
            <div class="catalogue">
              <div
                class="content-item"
                v-for="(item, index) in recommendGoods"
                :key="index"
                @click="recommended(item.goodsId, item.goodsType)"
              >
                <el-tooltip
                  :content="item.goodsTitle"
                  placement="top"
                  v-ak-tooltip-auto-show
                >
                  <span class="ellipsis-aRow">{{ item.goodsTitle }} </span>
                </el-tooltip>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <boundCode
      @close="boundShow = false"
      :show="boundShow"
      :resourceId="goodsId"
      @succeed="succeed"
    ></boundCode>
  </div>
</template>
<script>
import {
  selectOcLexueResourceBookCourseById,
  selectOclexueResourceBookById,
  UpdateViewerNumber,
} from "@/api/willingLearn/index.js";
import {
  recommendGoods,
  recommendSubject,
  Listevaluate,
  CartAdd,
  BookshelfAdd,
  IsCartAdd,
  IsBookshelfAdd,
  checkIsBuy,
  delBookshelf,
  SelGoodsStatus,
  TeachingClassList,
} from "@/api/bookstore/bookstore";
import evaluateContent from "@/views/bookstore/detail/evaluateContent";
import authorIntro from "@/views/bookstore/detail/authorIntro";
import contentIntro from "@/views/bookstore/detail/contentIntro";
import noData from "@/components/noData.vue";
import courseDetails from "@/components/courseDetails.vue";
import boundCode from "@/views/willingLearn/bookstore/boundCode/index.vue";
export default {
  data() {
    return {
      boundShow: false,
      value: 0,
      detail: "",
      currImg: 0,
      isBookshelf: false,
      isShop: false, //判断是否购买
      subjectId: null,
      showMore: false, //显示更多
      colors: ["#D81E06", "#D81E06", "#D81E06"],
      isfold: true,
      isflag: 1,
      isCart: false,
      keyword: [],
      goodsId: null,
      goodsType: null,
      total: 0,
      activeName: "five",
      evaluateList: [],
      coverFiles: [], //左侧商品图片数组
      recommendGoods: [], //推荐商品
      recommendGoodsOne: [],
      recommendSubject: [], //推荐专题
      //查询参数
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        goodsId: null,
        // type: "subject",
      },
      query: {
        goodsId: null,
        type: "goods",
      },
      payType: "goods",
      addCartState: true,
      courseList: [],
      paperList: [],
      teachClassList: [],
      averageScore: 0,
      teachClass: "",
    };
  },
  computed: {
    pthis() {
      return this;
    },
  },
  components: {
    evaluateContent,
    authorIntro,
    contentIntro,
    noData,
    courseDetails,
    boundCode,
  },

  created() {
    this.$store.commit("subjectId_book", this.$route.query.goodsId);
    this.goodsId = this.$route.query.goodsId;
    this.goodsType = this.$route.query.goodsType;
    this.subjectId = this.$route.query.subjectId;
    this.selectOclexueResourceBookByIds();
    this.selectOcLexueResourceBookCourseByIds();
    this.evaluation();

    if (localStorage.getItem("USERID")) {
      //判断是否加入书架
      this.isAddBookshelf();
      //判断是否加入购物车
      this.isAddCart();
    }
  },
  watch: {
    //监听路由变化
    $route() {
      if (this.$route.query.goodsId) {
        this.$store.commit("subjectId_book", this.$route.query.goodsId);
        this.goodsId = this.$route.query.goodsId;
        this.goodsType = this.$route.query.goodsType;
        this.isCart = false; //切换商品后未加入购物车时会提示已加入购物车
        this.selectOclexueResourceBookByIds();
        this.selectOcLexueResourceBookCourseByIds();
        this.evaluation();
      }
    },
  },
  methods: {
    succeed() {
      this.isPayGoods();
    },
    BuyNow() {},
    openBoundShow() {
      let userId = this.$store.getters.userId;
      if (userId) {
        this.boundShow = true;
        this.$store.commit("SET_REDIRECTPARAMS", {
          goodsId: this.goodsId,
          goodsType: this.goodsType,
        });
      } else {
        this.$router.push({
          path: "/login",
          query: {
            redirectPath: "/bookstore/bookstoreDetail",
          },
        });
      }
    },
    //商品列表
    selectOclexueResourceBookByIds() {
      let data = {
        id: this.$route.query.goodsId,
      };
      selectOclexueResourceBookById(data).then((res) => {
        this.detail = res.data || {};
        this.coverFiles = this.detail.coverFiles;
        let resourceCover = {
          fullUrl: this.detail.resourceCover,
        };
        this.coverFiles.unshift(resourceCover);
        if (this.detail.resourceIntroduction) {
          this.detail.resourceIntroduction =
            res.data.resourceIntroduction.replace(
              /\<img/gi,
              '<img style="max-width:100%;height:auto" '
            ) || "";
        }
        //课程年费
        if (this.detail.particular) {
          this.detail.particular = this.detail.particular.split("-")[0];
        }
        //关键字
        if (this.detail.keyword) {
          this.keyword = this.detail.keyword.split(",");
        }
        //评分
        if (this.detail.averageScore) {
          this.averageScore = this.detail.averageScore;
        }
        let userId = this.$store.getters.userId;
        if (userId) {
          this.isPayGoods();
        }
        //查询某一项教学属性下的分类
        let catalogType = "train_property";
        TeachingClassList(catalogType).then((res) => {
          if (res.code == 200) {
            this.teachClassList = res.data;
            this.teachClass = this.teachClassList.find(
              (item) => item.id == this.detail.resourceType
            );
          }
        });
      });
      //推荐专题
      recommendSubject(this.goodsId).then((res) => {
        if (res.code == 200) {
          this.recommendSubject = res.data.slice(0, 2);
        }
      });
      //推荐商品
      recommendGoods(this.goodsId).then((res) => {
        if (res.code == 200) {
          this.recommendGoodsOne = res.data.slice(0, 1);
          this.recommendGoods = res.data.slice(1, 5);
        }
      });
      //乐学模块增加浏览量
      UpdateViewerNumber({ id: this.goodsId, type: "train" });
    },
    //加入书架
    addBookshelf() {
      if (this.detail.status != 2) {
        this.$message("该商品已下架");
        return;
      }
      //判断是否登录
      let userId = this.$store.getters.userId;
      if (!userId) {
        this.$store.commit("SET_REDIRECTPARAMS", {
          goodsId: this.goodsId,
          goodsType: this.goodsType,
        });
        this.$router.push({
          path: "/login",
          query: {
            redirectPath: "/bookstore/bookstoreDetail",
          },
        });
      } else {
        this.query.goodsId = this.goodsId;
        if (!this.isBookshelf) {
          SelGoodsStatus(this.goodsId).then((goodsState) => {
            if (goodsState.data.status === 2) {
              BookshelfAdd(this.query).then((res) => {
                if (res.code == 200) {
                  this.$message({
                    message: "加入书架成功",
                    type: "success",
                  });
                  this.isBookshelf = true;
                }
              });
            } else {
              this.$message("该商品已下架");
            }
          });
        } else {
          //移出书架
          this.query.goodsId = this.goodsId;
          delBookshelf(this.query).then((res) => {
            if (res.data) {
              this.isBookshelf = false;
              this.$message({
                message: "移出书架",
                type: "success",
              });
            }
          });
        }
      }
    },
    // 是否加入书架
    isAddBookshelf() {
      this.query.goodsId = this.goodsId;
      IsBookshelfAdd(this.query).then((res) => {
        if (res.code == 200) {
          if (res.data) {
            this.isBookshelf = true;
          } else {
            this.isBookshelf = false;
          }
        }
      });
    },
    //试卷总数
    selectOcLexueResourceBookCourseByIds() {
      selectOcLexueResourceBookCourseById(this.$route.query.goodsId).then(
        (res) => {
          this.courseList = JSON.parse(res.data.course.chapters) || [];
          this.paperList = res.data.paper || [];
        }
      );
    },
    //领取更多
    getMore() {
      this.$alert("此功能正在开发中，敬请期待", "提示信息");
    },
    enterShow() {
      if (this.detail.authorRecommendation.length > 135) {
        this.showMore = true;
      }
    },
    leaveShow() {
      this.showMore = false;
    },
    //点击查看更多
    open() {
      this.isfold = !this.isfold;
      this.isfold === true ? (this.isflag = 1) : (this.isflag = 2);
    },
    //获取评价列表
    evaluation() {
      this.queryParams.goodsId = this.goodsId;
      Listevaluate(this.queryParams).then((res) => {
        if (res.code == 200) {
          this.evaluateList = res.rows;
          this.evaluateList.forEach((item) => {
            item.score = Number(item.score);
          });
          this.total = res.total;
        }
      });
    },
    //跳转到对应专题
    Special(id) {
      this.$router.push({
        path: "/special/specialDetail",
        query: { subjectId: id },
      });
    },
    //跳转到对应商品
    recommended(id, type) {
      this.$router.push({
        path: "/bookstore/bookstoreDetail",
        query: { goodsId: id, goodsType: type },
      });
    },
    //跳转到订单页
    toPay() {
      let userId = this.$store.getters.userId;
      if (!userId) {
        this.$store.commit("SET_REDIRECTPARAMS", {
          goodsId: this.goodsId,
          goodsType: this.goodsType,
        });
        this.$router.push({
          path: "/login",
          query: {
            redirectPath: "/bookstore/bookstoreDetail",
          },
        });
      } else {
        //判断是否购买该商品
        this.payType = this.$route.query.payType
          ? this.$route.query.payType
          : "goods";
        checkIsBuy({ relevanceId: this.goodsId, type: this.payType }).then(
          (res) => {
            console.log("是否已购买响应", res);
            if (res.data === "2") {
              this.$message.error("数据异常");
              return;
            }
            if (res.data === "1") {
              SelGoodsStatus(this.goodsId).then((goodsState) => {
                if (goodsState.data.status == 2) {
                  this.$router.push({
                    path: "/submitOrder/index",
                    query: { id: this.goodsId, from: "bookstore" },
                  });
                  this.isShop = false;
                } else {
                  this.$message("该商品已下架");
                }
              });
            } else {
              this.isShop = true;
            }
          }
        );
      }
    },
    isPayGoods() {
      //判断是否已经购买该商品
      this.payType = this.$route.query.payType
        ? this.$route.query.payType
        : "goods";
      checkIsBuy({ relevanceId: this.goodsId, type: this.payType }).then(
        (res) => {
          console.log("是否已购买响应", res);
          if (res.data === "2") {
            this.$message.error("数据异常");
            return;
          }
          if (res.data === "1") {
            this.isShop = false;
          } else {
            this.isShop = true;
            this.boundShow = false;
          }
        }
      );
    },
    //加入购物车
    addCart() {
      let userId = this.$store.getters.userId;
      if (!userId) {
        this.$store.commit("SET_REDIRECTPARAMS", {
          goodsId: this.goodsId,
          goodsType: this.goodsType,
        });
        this.$router.push({
          path: "/login",
          query: {
            redirectPath: "/bookstore/bookstoreDetail",
          },
        });
      } else if (this.addCartState) {
        //加入购物车按钮 防止重复提交
        this.addCartState = false;
        checkIsBuy({ relevanceId: this.goodsId, type: this.payType }).then(
          (res) => {
            console.log("是否已购买响应", res);
            if (res.data === "2") {
              this.$message.error("数据异常");
              return;
            }
            if (res.data === "1") {
              if (!this.isCart) {
                SelGoodsStatus(this.goodsId).then((goodsState) => {
                  if (goodsState.data.status === 2) {
                    IsCartAdd(this.goodsId).then((res) => {
                      if (res.data) {
                        this.$message("该商品已经加入购物车");
                        return;
                      }
                      CartAdd(this.goodsId, userId).then((res) => {
                        if (res.code == 200) {
                          this.$store.commit(
                            "addGoodsIntoCartCart",
                            this.goodsId
                          );
                          this.$message({
                            message: "加入购物车成功",
                            type: "success",
                          });
                          this.isCart = true;
                        }
                      });
                    });
                  } else {
                    this.$message("该商品已下架");
                  }
                });
              } else {
                this.$message("该商品已经加入购物车");
              }
            } else {
              this.$message("该商品已购买无需再加入购物车");
            }
          }
        );
        setTimeout(() => {
          this.addCartState = true;
        }, 3000);
      }
    },
    //是否加入购物车
    isAddCart() {
      IsCartAdd(this.goodsId).then((res) => {
        if (res.code == 200) {
          if (res.data) {
            this.isCart = true;
          } else {
            this.isCart = false;
          }
        }
      });
    },
    //手机端观看
    phoneLook() {
      this.$alert("此功能正在开发中，敬请期待", "提示信息");
    },

    handleSizeChange(val) {
      this.queryParams.pageSize = val;
      this.evaluation();
    },
    handleCurrentChange(val) {
      this.queryParams.pageNum = val;
      this.evaluation();
    },
    changeSrc(index) {
      this.currImg = index;
    },
    share() {
      this.$alert("此功能正在开发中，敬请期待", "提示信息");
    },
    refreshList2() {
      this.evaluation();
    },
    formatDate(time) {
      var date = new Date(time);
      var year = date.getFullYear(),
        month = date.getMonth() + 1, //月份是从0开始的
        day = date.getDate(),
        hour = date.getHours(),
        min = date.getMinutes(),
        sec = date.getSeconds();
      var newTime =
        year +
        "-" +
        (month < 10 ? "0" + month : month) +
        "-" +
        (day < 10 ? "0" + day : day);
      //  + ' ' +
      // (hour < 10 ? '0' + hour : hour) + ':' +
      // (min < 10 ? '0' + min : min) + ':' +
      // (sec < 10 ? '0' + sec : sec);
      return newTime;
    },
    goLearn() {
      let url = this.$router.resolve({
        path: "/willingLearn/courseQuestions",
        query: {
          goodsId: this.goodsId,
        },
      });
      setTimeout(() => {
        window.open(url.href, "_blank");
      }, 600);
    },
  },
};
